h1 {
  font-weight: 600;
}

.app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.mainContent {
  flex-grow: 1;
}

.navbar {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.3);
}

.navbar img {
  margin-left: 20px;
}

.navLinks {
  display: flex;
  justify-content: flex-end;
  width: 50%;
}

.navLinks li {
  list-style: none;
  padding: 1rem
}

footer {
  height: 50px;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.gutter {
  padding-left: 20px;
  padding-right: 20px;
}
